<template>
  <!-- 绘图画布 -->
  <div id="draw">
    <el-container @click="shutAll">
      <el-header @click="shutAll" style="   padding:'0px 0px' ;height:30px">
        <span>
          <el-row>
            <el-col :span="8">
              <div v-if="!me">等待对手</div>
              <div v-if="me">自己回合</div>
            </el-col>
            <el-col :span="8">
              <div v-if="userType==1">执黑</div>
              <div v-if="userType==2">执白</div>
            </el-col>
            <el-col :span="8">
              <div v-if="userType==2">对手 {{otherName}} 执黑</div>
              <div v-if="userType==1">对手 {{otherName}} 执白</div>
            </el-col>
          </el-row>
        </span>
      </el-header>
      <el-main class="board">
        <canvas id="cvs" ref="canvas" width="380" height="380" @mousedown="chessClick($event)"></canvas>
        <chat :messageList="messageList"></chat>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { mapState } from "vuex";
import chat from "./chat.vue";

export default {
  data() {
    return {
      chess: {},
      context: {},
      chessBoard: [], // 记录是否走过
      count: 0, // 所有赢法数量
      wins: [], // 赢法数组
      myWin: [], // 我方赢法的统计数组
      computerWin: [], // 计算机赢法的统计数组
      over: false
    };
  },
  components: {
    chat
  },
  computed: {
    ...mapState([
      "keyword",
      "ws",
      "status",
      "messageList",
      "picture",
      "userType",
      "nickName",
      "groupId",
      "otherName",
      "me"
    ])
  },
  // 定义好Draw类以后，在ready阶段使用即可：
  mounted() {
    setTimeout(() => {
      this.init();
    });
    const that = this;
    if (!this.ws) {
      this.$message("连接失败");
      this.$store.commit("logout");
      return;
    }

    // 判断来自服务器的消息并操作
    this.ws.onmessage = msg => {
      var pathObj = JSON.parse(msg.data);
      var type = pathObj.type;
      var message = pathObj.msg;
      window.console.log("drow:" + msg.data);
      if (type == "err") {
        that.$message(message);
      } else if (type == "msg") {
        that.messageList.unshift(message);
      } else if (type == "group") {
        that.$store.commit("groupChange", message);
      } else if (type == "start") {
        this.init();
        that.$store.commit("gameStart", message);
      } else if (type == "show") {
        this.open(message);
      } else if (type == "player:1") {
        window.console.log(this.userType);
        if (this.userType == 2) {
          var pos = message.split(":");
          that.put(pos[0], pos[1], 1);
        }
      } else if (type == "player:2") {
        window.console.log('play2')
        if (this.userType == 1) {
          window.console.log('play2:in')
          var pos2 = message.split(":");
          that.put(pos2[0], pos2[1], 2);
        }
      }
    };
  },
  methods: {
    // 初始化
    init() {
      this.chess = this.$refs.canvas;
      window.console.log(this.chess);
      this.context = this.chess.getContext("2d");
      this.drawImage(() => {
        this.drawChessBoard();
      });
      this.fillArray();
    },
    // 填充数组
    fillArray() {
      for (let i = 0; i < 15; i++) {
        this.chessBoard[i] = [];
        for (let j = 0; j < 15; j++) {
          this.chessBoard[i][j] = 0;
        }
      }
    },
    // 绘制水印
    drawImage(callback) {
      //const { context } = this
      //const img = new Image()
      //img.src = "/images/logo.svg"
      //img.onload = (() => {
      //context.drawImage(img, (450 - img.width)/2, (450 - img.height)/2, img.width, img.height)

      //})
      callback();
    },
    // 绘制棋盘
    drawChessBoard() {
      const { context } = this;
      context.fillStyle = "#fff";
      context.fillRect(0, 0, 380, 380);
      context.strokeStyle = "#bfbfbf";
      var begain = 15;
      var row = 25;

      for (let i = 0; i < 15; i++) {
        context.moveTo(begain + i * row, begain);
        context.lineTo(begain + i * row, 365);
        context.stroke();
        context.moveTo(begain, begain + i * row);
        context.lineTo(365, begain + i * row);
        context.stroke();
        context.moveTo(begain + 7 * row, begain + 7 * row);
        context.arc(begain + 7 * row, begain + 7 * row, 3, 0, 2 * Math.PI);
        context.stroke();
        context.moveTo(begain + 3 * row, begain + 3 * row);
        context.arc(begain + 3 * row, begain + 3 * row, 3, 0, 2 * Math.PI);
        context.stroke();
        context.moveTo(begain + 3 * row, begain + 11 * row);
        context.arc(begain + 3 * row, begain + 11 * row, 3, 0, 2 * Math.PI);
        context.stroke();
        context.moveTo(begain + 11 * row, begain + 11 * row);
        context.arc(begain + 11 * row, begain + 11 * row, 3, 0, 2 * Math.PI);
        context.stroke();
        context.moveTo(begain + 11 * row, begain + 3 * row);
        context.arc(begain + 11 * row, begain + 3 * row, 3, 0, 2 * Math.PI);
        context.stroke();
      }
    },
    // 落子实现
    onStep(x, y, player) {
      window.console.log("player:"+player)
      if(!player){
        return
      }
      window.console.log("onstep" + x + y + player);
      const { context } = this;
      context.beginPath();
      context.arc(15 + x * 25, 15 + y * 25, 11, 0, 2 * Math.PI);
      context.closePath();
      const gradient = context.createRadialGradient(
        15 + x * 25 + 2,
        15 + y * 25 - 2,
        13,
        15 + x * 25 + 2,
        15 + y * 25 - 2,
        0
      );
      if (player == 1) {
        gradient.addColorStop(0, "#0a0a0a");
        gradient.addColorStop(1, "#636766");
      } else if (player == 2) {
        gradient.addColorStop(0, "#d1d1d1");
        gradient.addColorStop(1, "#f9f9f9");
      }else{
        return
      }
      context.fillStyle = gradient;
      context.fill();
    },
    put(x, y, player) {
      window.console.log('play2:put')
      if (this.over) {
        return;
      }
      window.console.log('play2:put:'+this.me)
      if (this.me) {
        return;
      }
      if (this.chessBoard[x][y] === 0) {
        this.chessBoard[x][y] = 1;
        this.onStep(x, y, player);
        this.$store.commit("changePut", true);
      }
    },
    // 我方落子
    chessClick(e) {
      if (this.over) {
        return;
      }
      if (!this.me) {
        return;
      }
      const ox = e.offsetX;
      const oy = e.offsetY;
      const x = Math.floor(ox / 25);
      const y = Math.floor(oy / 25);
      window.console.log(this.chessBoard);
      window.console.log(x);
      window.console.log(y);
      if (this.chessBoard[x][y] === 0) {
        this.chessBoard[x][y] = 1;
        this.onStep(x, y, this.userType);
        this.$store.commit("changePut", false);
        this.ws.send(
          "put::" + x + ":" + y + ":" + this.userType + ":" + this.groupId
        );
      }
    },
    open(userId) {
      var cav = this.$refs.canvas;
      this.$store.commit("setPicture", cav);
      this.$store.commit("showVictory", userId);
    }
  }
};
</script>

<style >
#cvs {
  cursor: default;
  border: 1px solid #999;
}
.board {
  padding: 0px 0px;
}
#keyword-box {
  margin: 10px 0;
}
</style>