<template>
  <div id="group">
    <el-container>
      <el-header style="padding:10px 10px ;height:80px">
        <div style="padding:10px 20px">房间号:{{groupId}}</div>
        <el-button v-if="isHost" @click="start">开始游戏</el-button>
        <span v-if="!isHost">等待房主开始游戏</span>
      </el-header > 
      <el-main style="height:200px">
        <el-table :data="tableData" size="mini" stripe>
          <el-table-column prop="name" label="玩家"></el-table-column>
          <el-table-column prop="isMaster" :formatter="getRole" label></el-table-column>
          <el-table-column width="80%" label>
            <template slot-scope="scope">
              <el-button v-if="scope.row.name == nickName" size="mini" @click="exit">退出</el-button>
            </template>
          </el-table-column>
        </el-table>
        
      </el-main>
      <el-footer>
        <chat :messageList="messageList"></chat>
      </el-footer>
      <!-- <el-footer>
        <el-button size="small"  icon="el-icon-user" @click="shareToFriends"></el-button>
        <el-button size="small"  icon="el-icon-orange" @click="shareToFriendsCircle"></el-button>
      </el-footer>-->
    </el-container>
  </div>
</template>

<script>
import { mapState } from "vuex";
import chat from "./chat.vue";
export default {
  data() {
    return {
      tableData: []
    };
  },
  components: {
    chat
  },
  computed: {
    ...mapState(["isHost", "nickName", "groupId", "ws", "messageList"])
  },
  methods: {
    checkCon() {
      if (!this.ws) {
        location.reload();
      }
    },
    search() {
      this.checkCon();
      var that = this;
      that.ws.send("group::" + that.groupId);
    },
    start() {
      this.checkCon();
      this.ws.send("start::" + this.groupId);
    },
    getRole(row) {
      return row.isMaster ? "房主" : "已加入";
    },
    exit() {
      this.checkCon();
      var that = this;
      this.ws.send("exit::" + this.nickName + ":" + this.groupId);
      that.$store.commit("exit");
    }
  },
  mounted() {
    this.checkCon();
    var that = this;
    this.search();

    var ws = this.ws;
    ws.onmessage = msg => {
      var messages = JSON.parse(msg.data);
      window.console.log("===>" + msg.data);

      var type = messages.type;
      var massage = messages.msg;
      if (type == "err") {
        that.$message(massage);
      } else if (type == "group" ) {
        that.tableData = massage.memberList;
        that.$store.commit("groupChange", massage);
      }else if (type == "start") {
        that.$store.commit("gameStart", massage);
      } else if (type == "msg") {
        that.messageList.unshift(massage);
      } 
    };
    ws.onclose = () => {
      location.reload();
    };
  }
};
</script>