<template>
  <div id="login">
    <el-container>
      <el-header style="padding:60px 30px">
        <el-input size="small" v-model="nickName" placeholder="请输入4个字以内的昵称"></el-input>
      </el-header>
      <el-main>
        <el-button size="small" @click="login">确定</el-button>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      success: false,
      nickName: "",
      status: 0
    };
  },
  computed: {
    ...mapState(["keyword", "ws", "status", "messageList", "picture"])
  },
  // 定义好Draw类以后，在ready阶段使用即可：
  methods: {
    login() {
      if(this.success){
        return
      }
      if (!this.nickName) {
        this.$message("昵称不能为空");
        return;
      }
      if (this.nickName.length > 4) {
        this.$message("昵称不能多于4个字");
        return;
      }
      var that = this;
      var baseURL = process.env.VUE_APP_BASE_WS_URL;
      window.console.log(process.env.VUE_APP_BASE_WS_URL);
      var ws = new WebSocket(baseURL + "/gobang");
      ws.onopen = ()=>{
        ws.send('login::'+that.nickName)
      }
      ws.onmessage = msg => {
        window.console.log(msg)
        var messages = JSON.parse(msg.data);
        window.console.log(messages)
        var type = messages.type;
        var message = messages.msg;
        if (type == "err") {
          window.console.log('msg err')
          that.$message(message);
        } else if(type == 'groupList') {
          that.$store.commit("login", {
            name: that.nickName,
            ws: ws
          });
          localStorage.setItem("nickName", that.nickName);
          that.success = true 
        }
      };
    }
  },
  mounted() {
    this.nickName = localStorage.getItem("nickName");
    if (this.nickName&&!this.ws) {
      this.login();
    }
  }
};
</script>

<style >
#canvas {
  background: pink;
  cursor: default;
}
#keyword-box {
  margin: 10px 0;
}
</style>