<template>
  <div id="groupList">
    <el-container>
      <el-header style="padding:20px 10px ;height:160px">
        <el-row style="margin:10px 0px ">
          <el-col :span="4">
            <div class>
              <el-avatar
                size="small"
                src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
              ></el-avatar>
            </div>
          </el-col>
          <el-col :span="12" style="padding:1px 0px ; text-align: left; font-size: 18px;">
            <div>{{nickName}}</div>
          </el-col>
          <el-col :span="8">
            <div>
              <el-button size="mini" @click="logout">退出</el-button>
            </div>
          </el-col>
        </el-row>
        <el-divider></el-divider>
        <el-form
          :inline="true"
          lable-width="0px"
          size="small"
          :model="formInline"
          class="demo-form-inline"
        >
          <el-form-item label="编号">
            <el-input
              label-width="50%"
              style="width: 120px"
              v-model="formInline.searchId"
              placeholder="编号"
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="search">查询</el-button>
          </el-form-item>
        </el-form>
      </el-header>
      <el-main style=" text-align:center margin: auto">
        <div class="grid-content bg-purple-dark">
          <div>
            <el-table
              :data="tableData"
              size="small"
              stripe
              style=" text-align:center width: 50% vertical-align:middle margin:0px auto"
            >
              <el-table-column min-width="45%" prop="id" label="编号"></el-table-column>
              <el-table-column min-width="45%" prop="host" label="房主"></el-table-column>
              <el-table-column min-width="45%" prop="memberSum" label="人数"></el-table-column>
              <el-table-column min-width="45%" prop="status" :formatter="getStatusName" label="状态"></el-table-column>
              <el-table-column label>
                <template slot-scope="scope">
                  <el-button
                    v-if="scope.row.status == '2' && scope.row.memberSum<2"
                    size="mini"
                    @click="join(scope.$index, scope.row)"
                  >加入</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-main>
      <el-footer>
        <el-button size="small" @click="createGroup">创建房间</el-button>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      tableData: [],
      formInline: {
        searchId: ""
      }
    };
  },
  computed: {
    ...mapState(["status", "name", "ws", "nickName"])
  },
  methods: {
    checkCon() {
      window.console.log("checkconn");
      if (!this.ws) {
        window.console.log("reload");
        location.reload();
      }
    },
    join(index, row) {
      this.checkCon();
      window.console.log(index);
      this.ws.send("join::" + this.nickName + ":" + row.id);
    },
    search() {
      this.checkCon();
      this.ws.send("search::" + this.formInline.searchId);
    },
    createGroup() {
      this.checkCon();
      var that = this;
      this.ws.send("create::" + that.nickName);
    },
    getStatusName(row) {
      if (row.status == "2") {
        return "等待";
      }
      if (row.status == "3" || row.status == "4") {
        return "进行中";
      }
    },
    logout() {
      localStorage.setItem("nickName", "");
      location.reload();
    }
  },
  mounted() {
    this.checkCon();
    var that = this;

    this.ws.onmessage = msg => {
      var messages = JSON.parse(msg.data);
      var type = messages.type;
      if (type == "groupList") {
        window.console.log(messages.msg);
        that.tableData = messages.msg;
      } else if (type == "create") {
        that.$store.commit("create", messages.msg.id);
      } else if (type == "group") {
        that.$store.commit("groupChange", messages.msg);
      }
    };
    this.search();
    this.ws.onclose = e => {
      window.console.log(e);
      location.reload();
    };
  }
};
</script>